import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            title: '患者服务包详情',
            info: null,
            orderId: this.$route.query.orderId,
            serviceId: this.$route.query.serviceId,
            user: this.$store.state.userInfo,
            showConfirm: false
        }
    },
    created(){
        if(this.orderId){
            this.title = '患者服务包详情'
        }
        if(this.serviceId){
            this.title = '服务包详情'
        }
        this.pull()
    },
    methods: {
        pull(){
            if(this.orderId){
                this.$get('PlatFormAPI/ServicePackItem/QueryPatientPayOrder',{
                    PayOrderId: this.orderId
                }).then(o => {
                    this.info = o
                })
            }
            if(this.serviceId){
                this.$get(`PlatFormAPI/ServicePackItem/QueryServicePackAndDetailByPackId`,{
                    ServicePackId: this.serviceId,
                    DrId: this.user.Data.User.DoctorId
                    // DrId: 18
                }).then(o => {
                    this.info = o
                })
            }
        },
        send(){
            this.$bridge.callhandler('SendMsg', this.$store.state.pInfo)
            this.$bridge.callhandler('SendMessage', JSON.stringify(this.$store.state.pInfo))
        }
    }
}



